<template>
  <v-app>
    <component :is="layout">
      <router-view />
    </component>
  </v-app>
</template>

<script>
export default {
  name: "App",
  components: {
    DefaultLayout: () => import("@/layouts/DefaultLayout.vue"),
    EmptyLayout: () => import("@/layouts/EmptyLayout.vue"),
  },
  computed: {
    layout() {
      return this.$route.meta.layout || "default-layout";
    },
  },
};
</script>

