import Vue from 'vue'
import VueRouter from 'vue-router'
import Dashboard from '../views/Dashboard.vue'

Vue.use(VueRouter)

const routes = [
    {
        path: '/',
        name: 'Dashboard',
        component: Dashboard
    },
    {
        path: '/login',
        name: 'Login',
        component: () => import(/* webpackChunkName: "login" */ '../views/Login.vue'),
        meta: {
            layout: 'empty-layout'
        }
    },
    {
        path: '/staff',
        name: 'Staff',
        component: () => import(/* webpackChunkName: "staff" */ '../views/Staff.vue'),
    },
    {
        path: '/edit-staff',
        name: 'EditStaff',
        component: () => import(/* webpackChunkName: "edit-staff" */ '../views/EditStaff.vue'),
    },
    {
        path: '/create-staff',
        name: 'CreateStaff',
        component: () => import(/* webpackChunkName: "create-staff" */ '../views/CreateStaff.vue'),
    },
    {
        path: '/view-staff',
        name: 'ViewStaff',
        component: () => import(/* webpackChunkName: "view-staff" */ '../views/ViewStaff.vue'),
    },
    {
        path: '/about',
        name: 'About',
        // route level code-splitting
        // this generates a separate chunk (about.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
    }
]

const router = new VueRouter({
    mode: 'hash',
    routes
})

export default router
